<style lang="scss" scoped>
  .pagination-wrap {
    margin-top: 10px;
    overflow: hidden;
    >div {
      float: right;
    }
  }
</style>
<template>
  <div>
    <div>
      <el-form :inline="true" :model="form" class="demo-form-inline">
        <el-form-item label="">
          <el-date-picker
            v-model="form.orderDate"
            type="daterange"
            value-format="yyyy-MM-dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.activeDate"
            type="daterange"
            value-format="yyyy-MM-dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.keyWord" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="goSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-table
        :data="tableData.list"
        style="width: 100%">
        <!-- <el-table-column
          prop="channel"
          label="">
        </el-table-column> -->
        <el-table-column
          prop="orderId"
          width="120"
          label="">
        </el-table-column>
        <!-- <el-table-column
          prop="nickName"
          label="">
        </el-table-column> -->
        <!-- <el-table-column
          prop="name"
          label="">
        </el-table-column> -->
        <el-table-column
          prop="phone"
          label="">
        </el-table-column>
        <el-table-column
          prop="item"
          label="">
          <template slot-scope="scope">
            <div>
              {{ scope.row.goodsName === '' ? scope.row.basicName :  scope.row.coupon }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="amount"
          label="()"
          width="100px">
          <template slot-scope="scope">
            <div>
              <p>{{ (scope.row.amount || 0) / 100 }}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="coupon"
          label="">
          <template slot-scope="scope">
              <div v-if="scope.row.goodsName === ''">{{ !scope.row.coupon ? "--" :  scope.row.coupon}}</div>
            <div v-else>{{ '--' }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="accAmount"
          label="()">
          <template slot-scope="scope">
            <div>
              <p>{{ scope.row.accAmount / 100 || '--'  }}</p>
              <el-button v-if="scope.row.status === ''" type="text" @click="open(scope.row, 3)"></el-button>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="time"
          width="100"
          label="">
          <template slot-scope="scope">
            <div>
              <p>{{ moment(scope.row.time).format('MM-DD') }}</p>
              <p>{{ moment(scope.row.time).format('HH:mm:ss') }}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="time"
          width="175"
          label="">
          <template slot-scope="scope">
            <div>
              <p>{{ scope.row.hdStartTime && scope.row.goodsName === ''? "" + scope.row.hdStartTime : '--' }}</p>
              <p>{{ scope.row.hdEndTime && scope.row.goodsName === ''? "" + scope.row.hdEndTime : '--' }}</p>
            </div>
          </template>
        </el-table-column>
        <!-- <el-table-column
          prop="status"
          label="">
          <template slot-scope="scope">
            <div>
              <el-button v-if="scope.row.status === ''" type="text"></el-button>
              <p v-else></p>
            </div>
          </template>
        </el-table-column> -->
        <el-table-column
          prop="status"
          label="">
           <template slot-scope="scope">
            <div>
              {{ scope.row.goodsName === '' ? "--" : scope.row.status }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="moneyBack"
          label="">
            <template slot-scope="scope">
              <div>{{ scope.row.moneyBack?'':'' }}</div>
            </template>
        </el-table-column>
        <el-table-column
          prop="moneyBackAmount"
          label="()">
          <template slot-scope="scope">
            <div>{{ scope.row.moneyBackAmount / 100 || '--' }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="checkNo"
          width="80"
          label="">
          <template slot-scope="scope">
            <div>
              {{ scope.row.checkNo || '--' }}
            </div>
          </template>
        </el-table-column>
        <!-- <el-table-column
          prop="desc"
          label="">
          <template slot-scope="scope">
            <div>
              <p v-if="scope.row.desc">{{ '' + scope.row.desc }}</p>
              <p v-if="!scope.row.desc">--</p>
              <el-button v-if="scope.row.desc && !scope.row.repNo" type="text" @click="open(scope.row, 2)"></el-button>
              <p v-if="scope.row.repNo">{{ '' + scope.row.repNo }}</p>
            </div>
          </template>
        </el-table-column> -->
      </el-table>
    </div>
    <div class="pagination-wrap">
      <el-pagination
        background
        @current-change="this.goPage"
        layout="prev, pager, next"
        :total="tableData.total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
import { orderOwner, orderCheck, orderRep, basicOrderPrice } from './../../api/merchant'
export default {
  data () {
    return {
      moment: moment,
      id: this.$route.params.id,
      pageNum: 1,
      form: {},
      tableData: {
        orderDate: [],
        activeDate: []
      }
    }
  },
  beforeMount () {
    this.getOrderList()
  },
  methods: {
    getOrderList (params) {
      orderOwner({
        basicId: this.id,
        ...params,
        createTime1: this.form.orderDate && this.form.orderDate[0],
        createTime2: this.form.orderDate && this.form.orderDate[1],
        ydTime1: this.form.activeDate && this.form.activeDate[0],
        ydTime2: this.form.activeDate && this.form.activeDate[1],
        keyWord: this.form.keyWord
      }).then((result) => {
        this.tableData = result.data
      })
    },
    goSearch () {
      this.getOrderList({ pageNum: 1 })
    },
    goPage (pageNum) {
      this.getOrderList({ pageNum })
    },
    open (item, type) {
      let msg = {
        1: '',
        2: '',
        3: ''
      }
      this.$prompt(msg[type], '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(({ value }) => {
        if (type === 1) {
          orderCheck({ orderNum: item.orderId, checkNo: value }).then((result) => {
            this.getOrderList()
            this.$message({
              type: 'success',
              message: ''
            })
          })
        } else if (type === 2) {
          orderRep({ orderNum: item.orderId, checkNo: value }).then((result) => {
            this.getOrderList()
            this.$message({
              type: 'success',
              message: ''
            })
          })
        } else if (type === 3) {
          console.log(Number(value))
          if (!Number(value)) {
            this.$message({
              type: 'error',
              message: ''
            })
            return
          }
          basicOrderPrice({ orderNum: item.orderId, accAmount: value * 100 }).then((result) => {
            this.getOrderList()
          })
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }

}
</script>



