<style lang="scss" scoped>
  .edit-wrap {
    width: 500px;
    min-height: 100vh;
  }
</style>
<template>
  <div class="edit-wrap">
    <el-form ref="form" :rules="rules" :model="form" label-width="200px">
      <el-form-item label="" prop="areaName">
        <el-input v-model="form.areaName" placeholder=""></el-input>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-radio-group v-model="form.isMaster">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="" prop="areaNum">
        <el-input-number v-model="form.areaNum" :precision="0" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input-number v-model="form.price" :precision="2" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="" prop="num">
        <el-input-number v-model="form.num" :precision="0" :min="0"></el-input-number>
      </el-form-item>
      <el-form-item label="" prop="maxNum">
        <el-input-number v-model="form.maxNum" :precision="0" :min="0"></el-input-number>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-date-picker
          v-model="form.endTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item> -->
      <el-form-item label="">
        <el-input type="textarea" rows="4" v-model="form.descInfo" placeholder="(25)500"></el-input>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-radio-group v-model="form.deposit">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
        <p></p>
      </el-form-item> -->
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { priceEditShare, priceAddShare, priceDetail } from './../../api/merchant'
export default {
  props: ['id', 'isAdd', 'type', 'callBack', 'priceId'],
  data () {
    return {
      form: {
        // isMaster: '1',
        // deposit: '0'
      },
      rules: {
        areaName: [{ required: true, message: '' }],
        areaNum: [{ required: true, message: '' }],
        price: [{ required: true, message: '' }],
        num: [{ required: true, message: '' }],
        maxNum: [{ required: true, message: '' }]
      }
    }
  },
  beforeMount () {
    if (!this.isAdd) {
      priceDetail({
        priceId: this.priceId
      }).then((result) => {
        this.form = result.data && result.data.price
        this.form.price = this.form.price / 100 || null
      })
    }
  },
  methods: {
    onSubmit () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.isAdd) {
            priceAddShare({
              basicId: this.id,
              priceingMethod: this.type,
              ...this.form
            }).then(() => {
              this.callBack()
            })
          } else {
            priceEditShare({
              priceBasicId: this.priceId,
              basicId: this.id,
              priceingMethod: this.type,
              ...this.form
            }).then(() => {
              this.callBack()
            })
          }
        }
      })
    }
  }
}
</script>



