<style lang="scss" scoped>
  .edit-wrap {
    min-height: 100vh;
    .form-wrap {
      width: 500px;
    }
  }
</style>
<template>
  <div class="edit-wrap">
    <div class="form-wrap">
      <el-form ref="form" :rules="rules" :model="form" label-width="200px">
        <el-form-item label="" prop="areaName">
          <el-input v-model="form.areaName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="areaNum">
          <el-input-number v-model="form.areaNum" :precision="0" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item v-if="type === '5'" label="" prop="num">
          <el-input-number v-model="form.num" :precision="0" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item v-if="type === '5'" label="" prop="maxNum">
          <el-input-number v-model="form.maxNum" :precision="0" :min="0"></el-input-number>
        </el-form-item>
        <!-- <el-form-item label="" prop="isBind">
          <el-radio-group v-model="form.isBind">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" v-if="form.isBind === '1'" prop="reserveMethod">
          <el-select v-model="form.reserveMethod" placeholder="">
            <el-option
              v-for="index in 9"
              :key="index"
              :label="(index + 1) + ''"
              :value="index + 1">
            </el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="">
          <el-input type="textarea" rows="4" v-model="form.descInfo" placeholder="(25)500"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="deposit">
          <el-radio-group v-model="form.deposit">
            <el-radio label="1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
          <p></p>
        </el-form-item> -->
      </el-form>
    </div>
    <div>
      <el-table
      :border="true"
      :data="tableData"
      style="width: 100%">
        <el-table-column
          prop="price1"
          label=""
        >
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="1" v-model="isWeekend[0]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price1"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="price2"
          label=""
        >
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="2" v-model="isWeekend[1]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price2"></el-input>
          </template>>
        </el-table-column>
        <el-table-column
          prop="price3"
          label="">
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="3" v-model="isWeekend[2]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price3"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="price4"
          label=""
        >
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="4" v-model="isWeekend[3]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price4"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="price5"
          label=""
        >
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="5" v-model="isWeekend[4]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price5"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="price6"
          label="">
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="6" v-model="isWeekend[5]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price6"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          prop="price7"
          label="">
          <template slot="header" slot-scope="scope">
            
            <el-checkbox true-label="7" v-model="isWeekend[6]"></el-checkbox>
          </template>
          <template slot-scope="scope">
            <el-input v-model="scope.row.price7"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-button type="primary" @click="onSubmit()"></el-button>
  </div>
</template>
<script>
import { priceAddDay, priceAddDayEdit, priceDetail } from './../../api/merchant'
export default {
  props: ['id', 'isAdd', 'type', 'callBack', 'priceId'],
  data () {
    return {
      isWeekend: [],
      tableData: [],
      form: {
        isBind: '0'
        // deposit: '0'
      },
      rules: {
        areaName: [{ required: true, message: '' }],
        areaNum: [{ required: true, message: '' }],
        num: [{ required: true, message: '' }],
        maxNum: [{ required: true, message: '' }],
        reserveMethod: [{ required: true, message: '' }]
      }
    }
  },
  beforeMount () {
    if (!this.isAdd) {
      priceDetail({
        priceId: this.priceId
      }).then((result) => {
        let { areaName, areaNum, num, maxNum, isBind, reserveMethod, isWeekend, descInfo, deposit } = result.data.price
        this.form = { areaName, areaNum, num, maxNum, isBind, reserveMethod: (Number(reserveMethod) || ''), descInfo, deposit }
        let _isWeekend = (isWeekend && isWeekend.split(':')) || []
        _isWeekend.forEach(item => {
          this.$set(this.isWeekend, item - 1, item)
        })
        let obj = {}
        result.data.price.list.forEach(item => {
          obj['price' + item.weekendId] = item.price / 100 || ''
        })
        this.tableData.push({
          ...obj
        })
      })
    } else {
      this.tableData = [{}]
    }
  },
  methods: {
    onSubmit () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (this.isAdd) {
            priceAddDay({
              basicId: this.id,
              priceingMethod: this.type,
              ...this.form,
              list: this.tableData,
              isWeekend: this.isWeekend.filter(item => item).join(':')
            }).then(() => {
              this.callBack()
            })
          } else {
            priceAddDayEdit({
              priceBasicId: this.priceId,
              basicId: this.id,
              priceingMethod: this.type,
              ...this.form,
              list: this.tableData,
              isWeekend: this.isWeekend.filter(item => item).join(':')
            }).then(() => {
              this.callBack()
            })
          }
        }
      })
    }
  }
}
</script>



