<style lang="scss" scoped>
  .input-wrap {
    width: 300px;
  }
  .box-wrap {
    width: 400px;
    padding: 10px;
    border: 1px solid #d8d8d8;
    label {
      display: inline-block;
      width: 80px;
      text-align: right;
    }
    >div {
      margin-bottom: 10px;
    }
  }
  .postion {
    position: relative;
    z-index: 0;
  }
</style>
<template>
  <div>
    <h2></h2>
    <el-form ref="active" :model="active" :rules="rules" label-width="130px">
      <el-form-item label="" prop="proId">
        <el-select v-model="active.proId" placeholder="">
          <el-option v-for="item in projectLists" :key="item.sortId" :label="item.sortName" :value="item.sortId" ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="isFree">
        <el-select v-model="active.isFree" placeholder="">
          <el-option label="" :value="1" ></el-option>
          <el-option label="" :value="0" ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="proName">
        <el-input class="input-wrap" v-model="active.proName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="thumbnail">
        <el-upload
          class="upload-wrap"
          :action="config.apiRoot + '/upload'"
          :before-upload="beforeUpload"
          :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'thumbnail') }"
          :on-remove="(file, fileList) => { handleRemove(file, fileList, 'thumbnail') }"
          :file-list="active.thumbnail || []"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="areaId">
        <el-select placeholder="" @change="changeProvince" v-model="provinceId">
          <el-option v-for="item in provinceList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
        <el-select placeholder="" @change="changeCity" v-model="cityId">
          <el-option v-for="item in cityList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
        <el-select v-model="active.areaId" placeholder="">
          <el-option v-for="item in areaList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input class="input-wrap" v-model="active.address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input class="input-wrap" v-model="active.phone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input class="input-wrap" v-model="active.otherPhone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="startTime">
        <el-date-picker
          v-model="active.startTime"
          value-format="yyyy-MM-dd"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item prop="endTime">
        <el-date-picker
          v-model="active.endTime"
          value-format="yyyy-MM-dd"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="active.other5"
          value-format="yyyy-MM-dd"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <div id="editorElem" class="postion" style="text-align:left"></div>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="upload-wrap"
          :action="config.apiRoot + '/upload'"
          :before-upload="beforeUpload"
          :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'amount') }"
          :on-remove="(file, fileList) => { handleRemove(file, fileList, 'amount') }"
          :file-list="active.amountImg"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
        <el-input class="input-wrap" placeholder="" type="textarea" v-model="active.stopAmount"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" type="primary" @click="addItem()"></el-button>
        <div class="box-wrap" v-for="(item, index) in active.other3" :key="index">
          <div>
            <el-input class="input-wrap" placeholder="" type="text" v-model="item.name"></el-input>
            <el-button size="small" type="primary" @click="deleItem(index)"></el-button>
          </div>
          <div>
            <el-input class="input-wrap" placeholder="" type="textarea" v-model="item.desc"></el-input>
          </div>
          <div>
            <el-upload
              class="upload-wrap"
              :action="config.apiRoot + '/upload'"
              :before-upload="beforeUpload"
              :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'service', index) }"
              :on-remove="(file, fileList) => { handleRemove(file, fileList, 'service', index) }"
              :file-list="item.img"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-input class="input-wrap" type="textarea" v-model="active.other"></el-input>
      </el-form-item>
      <el-form-item label="" prop="rateType">
        <el-select v-model="active.rateType" placeholder="">
          <el-option label="1" :value="1"></el-option>
          <el-option label="2" :value="2"></el-option>
          <el-option label="3" :value="3"></el-option>
          <el-option label="4" :value="4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div>
          1
        </div>
        <div>
          2100%
        </div>
        <div>
          32424~125%12~610%6~220%250%100%
        </div>
        <div>
          40:00143360:0014~7336~16810%0:007~3168~7220%0:003~172~2450%0:0012480%0:00100%0:00
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" :loading="loading" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { areaProvince, areaCity, areaArea, projectList } from './../../api/common'
import { projectEdit, projectVenueDetail } from './../../api/merchant'
import E from 'wangeditor'
import config from './../../config'
export default {
  props: ['id'],
  data () {
    return {
      loading: false,
      editor: null,
      config: config,
      active: {
        cg: 3
        // other3: [{}]
      },
      projectLists: [],
      provinceList: [],
      provinceId: '',
      cityId: '',
      cityList: [],
      areaList: [],
      rules: {
        proId: [{ required: true, message: '' }],
        isFree: [{ required: true, message: '' }],
        proName: [{ required: true, message: '' }],
        areaId: [{ required: true, message: '' }],
        address: [{ required: true, message: '' }],
        phone: [{ required: true, message: '' }],
        startTime: [{ required: true, message: '' }],
        endTime: [{ required: true, message: '' }],
        rateType: [{ required: true, message: '' }],
        thumbnail: [{ required: true, validator: this.validateThumbnail }]
        // other5: [{ required: true, message: '' }]
      }
    }
  },
  beforeMount () {
    this.getProjectList()
    this.getProvince()
  },
  mounted () {
    this.editor = new E('#editorElem')
    this.editor.customConfig.uploadImgServer = this.config.apiRoot + '/upload'
    this.editor.customConfig.uploadFileName = 'file'
    this.editor.customConfig.uploadImgHooks = {
      customInsert: (insertImg, result, editor) => {
        let url = this.config.imgRoot + (result.data && result.data.name)
        insertImg(url)
      }
    }
    // editor.customConfig.uploadImgShowBase64 = true
    this.editor.customConfig.onchange = (html) => {
      this.active.other1 = html
    }
    this.editor.create()
    this.getDetail()
  },
  methods: {
    beforeUpload (file) {
      const isLt2kb = file.size / 1024 <= 200
      if (!isLt2kb) {
        this.$message.error(' 200kb!')
      }
      return isLt2kb
    },
    validateThumbnail (rule, value, callback) {
      let { thumbnail = [] } = this.active
      if (!value || !thumbnail.length) {
        return callback(new Error(''))
      } else {
        callback()
      }
    },
    addItem () {
      this.active.other3.push({})
    },
    deleItem (key) {
      this.active.other3 = this.active.other3.filter((item, index) => index !== key)
    },
    getDetail () {
      projectVenueDetail({ id: this.id }).then((result) => {
        this.provinceId = result.data.province
        this.cityId = result.data.city
        result.data.basic.amountImg = result.data.basic.amountImg && JSON.parse(result.data.basic.amountImg)
        this.active = {
          cg: result.data.basic.isCg,
          ...result.data,
          ...result.data.otherModular,
          ...result.data.basic,
          other3: result.data.otherModular.other3 ? JSON.parse(result.data.otherModular.other3) : '',
          thumbnail: result.data.otherModular.thumbnail ? JSON.parse(result.data.otherModular.thumbnail) : ''
        }
        this.editor.txt.html(this.active.other1)
        if (this.provinceId) {
          this.getCity(this.provinceId)
        }
        if (this.cityId) {
          this.getArea(this.cityId)
        }
      })
    },
    handleRemove (file, fileList, type, key) {
      if (file && file.status === 'success') {
        let _fileList = fileList.map(item => {
          return {
            name: item.name,
            url: config.imgRoot + item.response.data.name
          }
        })
        if (type === 'service') {
          this.active.other3[key].img = _fileList
        } else if (type === 'amount') {
          this.active.amountImg = _fileList
        } else if (type === 'thumbnail') {
          this.active.thumbnail = _fileList
        }
      } else {
        return false
      }
    },
    handleSuccess (res, file, fileList, type, key) {
      if (type === 'service') {
        if (!this.active.other3[key].img) {
          this.active.other3[key].img = []
        }
        this.active.other3[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'amount') {
        if (!this.active.amountImg) {
          this.active.amountImg = []
        }
        this.active.amountImg.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'thumbnail') {
        if (!this.active.thumbnail) {
          this.active.thumbnail = []
        }
        this.active.thumbnail = [{
          name: res.data.name,
          url: config.imgRoot + res.data.name
        }]
      }
    },
    getProjectList () {
      projectList().then((result) => {
        this.projectLists = result.data && result.data[5].list
      })
    },
    // 
    getProvince () {
      areaProvince().then((result) => {
        this.provinceList = result.data
      })
    },
    getCity (id) {
      areaCity({ id }).then((result) => {
        this.cityList = result.data
      })
    },
    getArea (id) {
      areaArea({ id }).then((result) => {
        this.areaList = result.data
      })
    },
    changeProvince (id) {
      this.cityId = ''
      this.active.areaId = ''
      this.areaList = []
      this.getCity(id)
    },
    changeCity (id) {
      this.getArea(id)
    },
    onSubmit () {
      this.$refs['active'].validate((valid) => {
        if (valid) {
          let _active = Object.assign({}, this.active)
          _active.other3 = JSON.stringify(_active.other3)
          _active.amountImg = JSON.stringify(_active.amountImg)
          projectEdit(_active).then((result) => {
            this.$router.push('/project')
          }, () => {
            this.loading = false
          })
        }
      })
    }
  }
}
</script>



