    <el-form ref="building" :model="building" :rules="rules" label-width="200px">
      <el-form-item label="" prop="proId">
        <el-select v-model="building.proId" placeholder="">
          <el-option v-for="item in projectLists" :key="item.sortId" :label="item.sortName" :value="item.sortId" ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="proName">
        <el-input class="input-wrap" v-model="building.proName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="thumbnail">
        <el-upload
          class="upload-wrap"
          :action="config.apiRoot + '/upload'"
          :before-upload="beforeUpload"
          :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'thumbnail') }"
          :on-remove="(file, fileList) => { handleRemove(file, fileList, 'thumbnail') }"
          :file-list="building.thumbnail"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="areaId">
        <el-select placeholder="" @change="changeProvince" v-model="provinceId">
          <el-option v-for="item in provinceList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
        <el-select placeholder="" @change="changeCity" v-model="cityId">
          <el-option v-for="item in cityList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
        <el-select v-model="building.areaId" placeholder="">
          <el-option v-for="item in areaList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="extend1">
        <el-select v-model="building.extend1" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="10" value="2"></el-option>
          <el-option label="11" value="3"></el-option>
          <el-option label="21" value="4"></el-option>
          <el-option label="22" value="5"></el-option>
          <el-option label="32" value="6"></el-option>
        </el-select>
        <!-- <el-input class="input-wrap" v-model="building.extend1" placeholder=""></el-input> -->
      </el-form-item>
      <!-- <el-form-item prop="extend2">
        <el-input class="input-wrap" v-model="building.extend2" placeholder=""></el-input>
      </el-form-item> -->
      <el-form-item label="" prop="equipments">
        <el-select v-model="building.equipments" multiple placeholder="">
          <el-option-group
            v-for="group in equipmentList"
            :key="group.label"
            :label="group.label">
            <el-option
              v-for="item in group.options"
              :key="item.label"
              :label="item.value"
              :value="item.label">
            </el-option>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input class="input-wrap" type="textarea" placeholder="" v-model="building.otherEq"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input class="input-wrap" v-model="building.phone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input class="input-wrap" v-model="building.otherPhone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="stayUpNum">
        <el-input class="input-wrap" v-model="building.stayUpNum" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="startTime">
        <el-date-picker
          v-model="building.startTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item prop="endTime">
        <el-date-picker
          v-model="building.endTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="building.other5"
          value-format="yyyy-MM-dd"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="addItem(1)"></el-button>
        <div class="adress-wrap" v-for="(item, index) in building.other" :key="index">
          <div>
            <label></label><el-input class="input-wrap" type="text" v-model="item.name"></el-input>
            <el-button type="primary" @click="deleItem(1, index)"></el-button>
          </div>
          <div>
            <label></label>
            <el-select placeholder="" @change="changeProvince($event, index)" v-model="item.provinceId">
              <el-option v-for="item in provinceList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
            </el-select>
            <el-select placeholder="" @change="changeCity($event, index)" v-model="item.cityId">
              <el-option v-for="item in item.cityList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
            </el-select>
            <!-- <el-select v-model="item.areaId" placeholder="">
              <el-option v-for="item in item.areaList" :key="item.areaId" :label="item.name" :value="item.areaId"></el-option>
            </el-select> -->
          </div>
          <div>
            <label></label><el-input class="input-wrap" type="textarea" v-model="item.adress"></el-input>
          </div>
          <div>
            <label></label><el-input class="input-wrap" type="textarea" v-model="item.desc"></el-input>
          </div>
          <div class="upload-wrap">
            <label></label>
            <el-upload
              class="upload-item"
              :action="config.apiRoot + '/upload'"
              :before-upload="beforeUpload"
              :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'otherImg', index) }"
              :on-remove="(file, fileList) => { handleRemove(file, fileList, 'otherImg', index) }"
              :file-list="item.img"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="addItem(2)"></el-button>
        <div class="adress-wrap" v-for="(item, index) in building.other1" :key="index">
          <div>
            <label></label><el-input class="input-wrap" type="text" v-model="item.name"></el-input>
            <el-button type="primary" @click="deleItem(2, index)"></el-button>
          </div>
          <div>
            <label></label><el-input class="input-wrap" type="textarea" v-model="item.desc"></el-input>
          </div>
          <div class="upload-wrap">
            <label></label>
            <el-upload
              class="upload-item"
              :action="config.apiRoot + '/upload'"
              :before-upload="beforeUpload"
              :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'other1Img', index) }"
              :on-remove="(file, fileList) => { handleRemove(file, fileList, 'other1Img', index) }"
              :file-list="item.img"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="addItem(3)"></el-button>
        <div class="adress-wrap" v-for="(item, index) in building.other2" :key="index">
          <p>{{ index + 1 }}</p>
          <div>
            <label></label><el-input class="input-wrap" type="text" placeholder="6" v-model="item.adress1"></el-input>
            <el-button type="primary" @click="deleItem(3, index)"></el-button>
          </div>
          <div>
            <label></label><el-input class="input-wrap" type="text" placeholder="6" v-model="item.adress2"></el-input>
          </div>
          <el-button type="primary" @click="addItem(4, index)"></el-button>
          <div class="item-wrap" v-for="(ele, key) in item.list" :key="key">
            <div>
              <label></label>
              <el-time-select
                v-model="ele.time"
                :picker-options="{
                  start: '00:00',
                  end: '24:00'
                }"
                placeholder="">
              </el-time-select>
              <el-button type="primary" @click="deleItem(4, index, key)"></el-button>
            </div>
            <div>
              <label></label><el-input class="input-wrap" type="text" v-model="ele.title"></el-input>
            </div>
            <div>
              <label></label><el-input class="input-wrap" type="textarea" v-model="ele.desc"></el-input>
            </div>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="upload-wrap"
          :action="config.apiRoot + '/upload'"
          :before-upload="beforeUpload"
          :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'amount') }"
          :on-remove="(file, fileList) => { handleRemove(file, fileList, 'amount') }"
          :file-list="building.amountImg"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
        <el-input class="input-wrap" placeholder="" type="textarea" v-model="building.stopAmount"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" type="primary" @click="addItem(5)"></el-button>
        <div class="adress-wrap" v-for="(item, index) in building.other3" :key="index">
          <div>
            <el-input class="input-wrap" placeholder="" type="text" v-model="item.name"></el-input>
            <el-button size="small" type="primary" @click="deleItem(5, index)"></el-button>
          </div>
          <div>
            <el-input class="input-wrap" placeholder="" type="textarea" v-model="item.desc"></el-input>
          </div>
          <div>
            <el-upload
              class="upload-wrap"
              :action="config.apiRoot + '/upload'"
              :before-upload="beforeUpload"
              :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'service', index) }"
              :on-remove="(file, fileList) => { handleRemove(file, fileList, 'service', index) }"
              :file-list="item.img"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-input class="input-wrap" placeholder="" type="textarea" v-model="building.other4"></el-input>
      </el-form-item>
      <el-form-item label="" prop="rateType">
        <el-select v-model="building.rateType" placeholder="">
          <el-option label="1" :value="1"></el-option>
          <el-option label="2" :value="2"></el-option>
          <el-option label="3" :value="3"></el-option>
          <el-option label="4" :value="4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div>
          1
        </div>
        <div>
          2100%
        </div>
        <div>
          32424~125%12~610%6~220%250%100%
        </div>
        <div>
          40:00143360:0014~7336~16810%0:007~3168~7220%0:003~172~2450%0:0012480%0:00100%0:00
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" :loading="loading" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { areaProvince, areaCity, areaArea, projectList, equipmentList } from './../../api/common'
import { projectAdd } from './../../api/merchant'
import config from './../../config'
export default {
  data () {
    return {
      loading: false,
      config: config,
      equipmentList: [],
      building: {
        cg: 2,
        areaId: '',
        other: [{}],
        other1: [{}],
        other2: [{
          list: [{}]
        }],
        other3: [{}],
        equipments: []
      },
      projectLists: [],
      provinceList: [],
      provinceId: '',
      cityId: '',
      cityList: [],
      areaList: [],
      rules: {
        proId: [{ required: true, message: '' }],
        proName: [{ required: true, message: '' }],
        areaId: [{ required: true, message: '' }],
        extend1: [{ required: true, message: '' }],
        extend2: [{ required: true, message: '' }],
        equipments: [{ required: true, message: '' }],
        phone: [{ required: true, message: '' }],
        stayUpNum: [{ required: true, message: '' }],
        startTime: [{ required: true, message: '' }],
        endTime: [{ required: true, message: '' }],
        rateType: [{ required: true, message: '' }],
        thumbnail: [{ required: true, validator: this.validateThumbnail }]
        // other5: [{ required: true, message: '' }]
      }
    }
  },
  beforeMount () {
    this.getProvince()
    this.getProjectList()
    this.getEquipmentList()
  },
  methods: {
    beforeUpload (file) {
      const isLt2kb = file.size / 1024 <= 200
      if (!isLt2kb) {
        this.$message.error(' 200kb!')
      }
      return isLt2kb
    },
    validateThumbnail (rule, value, callback) {
      let { thumbnail = [] } = this.building
      console.log(thumbnail)
      if (!value || !thumbnail.length) {
        return callback(new Error(''))
      } else {
        callback()
      }
    },
    addItem (type, key) {
      if (type === 1) {
        this.building.other.push({})
      } else if (type === 2) {
        this.building.other1.push({})
      } else if (type === 3) {
        this.building.other2.push({list: [{}]})
      } else if (type === 4) {
        this.building.other2[key].list.push({})
      } else if (type === 5) {
        this.building.other3.push({})
      }
    },
    deleItem (type, key, key1) {
      if (type === 1) {
        this.building.other = this.building.other.filter((item, index) => index !== key)
      } else if (type === 2) {
        this.building.other1 = this.building.other1.filter((item, index) => index !== key)
      } else if (type === 3) {
        this.building.other2 = this.building.other2.filter((item, index) => index !== key)
      } else if (type === 4) {
        this.building.other2[key].list = this.building.other2[key].list.filter((item, index) => index !== key1)
      } else if (type === 5) {
        this.building.other3 = this.building.other3.filter((item, index) => index !== key)
      }
    },
    handleRemove (file, fileList, type, key) {
      if (file && file.status === 'success') {
        let _fileList = fileList.map(item => {
          return {
            name: item.name,
            url: config.imgRoot + item.response.data.name
          }
        })
        if (type === 'otherImg') {
          this.building.other[key].img = _fileList
        } else if (type === 'other1Img') {
          this.building.other1[key].img = _fileList
        } else if (type === 'amount') {
          this.building.amountImg = _fileList
        } else if (type === 'service') {
          this.building.other3[key].img = _fileList
        } else if (type === 'thumbnail') {
          this.building.thumbnail = _fileList
        }
      } else {
        return false
      }
    },
    handleSuccess (res, file, fileList, type, key) {
      if (type === 'otherImg') {
        if (!this.building.other[key].img) {
          this.building.other[key].img = []
        }
        this.building.other[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'other1Img') {
        if (!this.building.other1[key].img) {
          this.building.other1[key].img = []
        }
        this.building.other1[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'amount') {
        if (!this.building.amountImg) {
          this.building.amountImg = []
        }
        this.building.amountImg.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'service') {
        if (!this.building.other3[key].img) {
          this.building.other3[key].img = []
        }
        this.building.other3[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'thumbnail') {
        if (!this.building.thumbnail) {
          this.building.thumbnail = []
        }
        this.building.thumbnail = [{
          name: res.data.name,
          url: config.imgRoot + res.data.name
        }]
      }
    },
    getEquipmentList () {
      equipmentList().then((result) => {
        this.equipmentList = result.data && [result.data[5], result.data[8]]
      })
    },
    // 
    getProvince () {
      areaProvince().then((result) => {
        this.provinceList = result.data
      })
    },
    getCity (id, index) {
      areaCity({ id }).then((result) => {
        if (index === undefined) {
          this.cityList = result.data
        } else {
          let _other = JSON.parse(JSON.stringify(this.building.other))
          _other[index].cityList = result.data
          this.building.other = _other
        }
      })
    },
    getArea (id, index) {
      areaArea({ id }).then((result) => {
        if (index === undefined) {
          this.areaList = result.data
        } else {
          let _other = JSON.parse(JSON.stringify(this.building.other))
          _other[index].areaList = result.data
          this.building.other = _other
        }
      })
    },
    changeProvince (id, index) {
      if (index === undefined) {
        this.cityId = ''
        this.building.areaId = ''
        this.areaList = []
        this.getCity(id)
      } else {
        this.building.other[index].cityId = ''
        this.building.other[index].areaId = ''
        this.building.other[index].areaList = []
        this.getCity(id, index)
      }
    },
    changeCity (id, index) {
      if (index === undefined) {
        this.building.areaId = ''
        this.getArea(id)
      } else {
        this.building.other[index].areaId = ''
        this.getArea(id, index)
      }
    },
    getProjectList () {
      projectList().then((result) => {
        this.projectLists = result.data && result.data[4].list
      })
    },
    onSubmit () {
      this.$refs['building'].validate((valid) => {
        if (valid) {
          this.loading = true
          let _building = Object.assign({}, this.building)
          _building.areaId = _building.areaId
          _building.other = _building.other.map(item => {
            delete item.areaList
            delete item.cityList
            return item
          })
          _building.equipments = _building.equipments && _building.equipments.join(':')
          _building.amountImg = JSON.stringify(_building.amountImg)
          _building.other = JSON.stringify(_building.other)
          _building.other1 = JSON.stringify(_building.other1)
          _building.other2 = JSON.stringify(_building.other2)
          _building.other3 = JSON.stringify(_building.other3)
          projectAdd(_building).then((result) => {
            this.$router.push('/project')
          }, () => {
            this.loading = false
          })
        }
      })
    }
  }
}
</script>



