<style lang="scss" scoped>

</style>
<template>
  <div>
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="descInfo"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="commRate"
        :label="'-'"
      >
      <template slot-scope="scope">
        <div>
          <p v-if="scope.row.commRate === '-1' || scope.row.commRate === '-2' ||  scope.row.commRate === '-3'">
            {{ scope.row.commRate === '-1'?'':(scope.row.commRate === '-2'?'':'')}}
          </p>
          <p v-else>
            {{ (scope.row.descInfo === '//' || scope.row.descInfo === '' || scope.row.descInfo === '()')?((scope.row.commRate * 100).toFixed(2) + '%'):scope.row.commRate }}
          </p>
        </div>
      </template>
      </el-table-column>
      <el-table-column
        prop="vipRate"
          label="-99-999">
        <template slot-scope="scope">
          <div>
            <p v-if="scope.row.vipRate === '-1' || scope.row.vipRate === '-2' ||  scope.row.vipRate === '-3'">
              {{ scope.row.vipRate === '-1'?'':(scope.row.vipRate === '-2'?'':'')}}
            </p>
            <p v-else>
              {{ (scope.row.descInfo === '//' || scope.row.descInfo === '' || scope.row.descInfo === '()')?((scope.row.vipRate * 100).toFixed(2) + '%'):scope.row.vipRate }}
            </p>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="seniorRate"
        label="VIP-199-1999">
        >
        <template slot-scope="scope">
          <div>
            <p v-if="scope.row.seniorRate === '-1' || scope.row.seniorRate === '-2' ||  scope.row.seniorRate === '-3'">
              {{ scope.row.seniorRate === '-1'?'':(scope.row.seniorRate === '-2'?'':'')}}
            </p>
            <p v-else>
              {{ (scope.row.descInfo === '//' || scope.row.descInfo === '' || scope.row.descInfo === '()')?((scope.row.seniorRate * 100).toFixed(2) + '%'):scope.row.seniorRate }}
            </p>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div style="width: 600px;padding: 10px 20px 80px">
      <p></p>
      <p>1</p>
      <p>212</p>
      <p>3VIP</p>
      <p>44000-855-919</p>
    </div>
  </div>
</template>
<script>
import { memberInfo } from './../../api/merchant'
export default {
  data () {
    return {
      tableData: [],
      vip: []
    }
  },
  created () {
    this.getMemberInfo()
  },
  methods: {
    getMemberInfo () {
      memberInfo().then(result => {
        this.vip = result.data.vip
        this.tableData = result.data.rate
      })
    }
  }
}
</script>



