<style lang="scss" scoped>
  .dialog-footer {
    overflow: hidden;
    button {
      float: right;
      margin-left: 10px;
    }
  }
</style>
<template>
  <div>
    <el-form ref="form" :rules="rules" label-width="240px" :model="form">
      <el-form-item label="" prop="name">
        <div style="width: 220px;">
          <el-input v-model="form.name" placeholder=""></el-input>
        </div>
      </el-form-item>
      <el-form-item label="" prop="id">
        <el-select v-model="form.id" multiple placeholder="">
          <el-option
            v-for="(item, index) in areaList"
            :key="index"
            :label="item.basic && item.basic.proName"
            :value="item.basic && item.basic.id">
          </el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-select v-model="form.price" value-key="priceBasic" placeholder="">
          <el-option
            v-for="(item, index) in priceList"
            :key="index"
            :label="item.priceBasic && item.priceBasic.areaName"
            :value="item"
          ></el-option>
        </el-select>
      </el-form-item> -->
      <el-form-item label="" prop="totalStock">
        <el-input-number width="220px" v-model="form.totalStock" :precision="0" :min="0" placeholder="" autocomplete="off"></el-input-number>
      </el-form-item>
      <el-form-item label="" prop="num">
        <el-input-number width="220px" v-model="form.num" :min="0" :precision="0" placeholder="" autocomplete="off"></el-input-number>
        <!-- {{ form.price && form.price.priceBasic.priceingMethodId === 4?'':'' }} -->
        {{ '' }}
      </el-form-item>
      <el-form-item label="" prop="disAmount">
        <el-input-number width="220px" v-model="form.disAmount" :precision="2" :min="0" placeholder="" autocomplete="off"></el-input-number>
      </el-form-item>
      <el-form-item label="" prop="weekend">
        <el-select v-model="form.weekend" multiple placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
          <el-option label="" value="4"></el-option>
          <el-option label="" value="5"></el-option>
          <el-option label="" value="6"></el-option>
          <el-option label="" value="7"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="time">
        <el-select v-model="form.time" multiple placeholder="">
          <el-option
            v-for="(item, index) in siteTypeList"
            :key="index"
            :label="item"
            :value="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="expTimes">
        <div style="width: 220px;">
          <el-date-picker
            v-model="form.expTimes"
            type="daterange"
            value-format="yyyy-MM-dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </div>
      </el-form-item>
      <!-- <el-form-item label="">
        <div style="width: 220px;">
          <el-radio-group v-model="form.isDis">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </div>
      </el-form-item>
      <el-form-item label="" v-if="form.isDis">
        <el-input-number v-model="form.disInfo" :min="0" placeholder="" autocomplete="off"></el-input-number>
        990%
      </el-form-item> -->
      <el-form-item label="">
        <div style="width: 220px;">
          <el-input type="textarea" v-model="form.desInfo"></el-input>
        </div>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submit"></el-button>
      <el-button @click="hideModal"> </el-button>
    </div>
  </div>
</template>
<script>
import { siteTypeList } from './projectConfig'
import { basiceTopOrder, addCoupon } from './../../api/merchant'
export default {
  data () {
    return {
      siteTypeList: siteTypeList,
      areaList: [],
      form: {},
      rules: {
        name: [{ required: true, message: '' }],
        id: [{ required: true, message: '' }],
        totalStock: [{ required: true, message: '' }],
        num: [{ required: true, message: '' }],
        disAmount: [{ required: true, message: '' }],
        weekend: [{ required: true, message: '' }],
        time: [{ required: true, message: '' }],
        expTimes: [{ required: true, message: '' }]
      }
    }
  },
  computed: {
    siteTypeArr: function () {
      let { price = {} } = this.form
      return (price.site || []).map(item => {
        return {
          id: item,
          name: siteTypeList[item]
        }
      })
    },
    priceList: function () {
      let _areaList = []
      _areaList = this.areaList.filter(item => {
        return item.basic && item.basic.id === this.form.id
      })
      let _priceList = _areaList[0] && _areaList[0].price.filter(item => {
        return item.priceBasic && (item.priceBasic.priceingMethodId === 1 || item.priceBasic.priceingMethodId === 4)
      })
      return _priceList
    }
  },
  beforeMount () {
    this.getBasicList()
  },
  methods: {
    getBasicList () {
      basiceTopOrder({}).then((result) => {
        this.areaList = result.data.filter(item => {
          return item.priceMethod.includes(1) || item.priceMethod.includes(4)
        })
      })
    },
    hideModal () {
      this.$emit('hideModal')
    },
    submit () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          let { form } = this
          let data = {
            ...form,
            type: 4,
            basicIdList: form.id,
            // priceIdList: [form.price.priceBasic && form.price.priceBasic.id],
            weekend: form.weekend && form.weekend.join(':'),
            time: form.time && form.time.join(','),
            startTime: form.expTimes && form.expTimes[0],
            endTime: form.expTimes && form.expTimes[1],
            disAmount: form.disAmount * 100 || null
          }
          delete data.id
          addCoupon(data).then((result) => {
            this.$emit('hideModal')
          })
        }
      })
    }
  }
}
</script>



