            :picker-options="{
              start: '00:00',
              end: '24:00'
            }"
            placeholder="">
          </el-time-select>
        </el-form-item>
        <el-form-item label="" prop="checkOutTime">
          <el-time-select
            v-model="vernue.checkOutTime"
            :picker-options="{
              start: '00:00',
              end: '24:00'
            }"
            placeholder="">
          </el-time-select>
          
        </el-form-item>
        <h2></h2>
        <el-form-item label="">
          <el-radio-group v-model="vernue.extend2.zf">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="vernue.extend2.xy">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="vernue.extend2.jh">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="vernue.extend2.cw">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="vernue.extend2.wj">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="18">
          <el-radio-group v-model="vernue.extend2.xh">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <div v-if="[20, 21, 22, 23].indexOf(vernue.proId) > -1">
        <h2></h2>
        <el-form-item label="" prop="hotelStar" v-if="vernue.proId === 20">
          <el-select v-model="vernue.hotelStar">
            <el-option label="" value="5"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="venueNum">
          <el-input class="input-wrap" type="text" v-model="vernue.venueNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="maxVenue">
          <el-input class="input-wrap" type="text" v-model="vernue.maxVenue"></el-input>
        </el-form-item>
        <el-form-item label="" prop="maxVenueNum">
          <el-input class="input-wrap" type="text" v-model="vernue.maxVenueNum"></el-input>
        </el-form-item>
        <el-form-item label="/">
          <el-select v-model="vernue.equipments" multiple placeholder="">
            <el-option-group
              v-for="group in equipmentList"
              :key="group.label"
              :label="group.label">
              <el-option
                v-for="item in group.options"
                :key="item.label"
                :label="item.value"
                :value="item.label">
              </el-option>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input class="input-wrap" type="textarea" placeholder="" v-model="vernue.otherEq"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="upload-wrap">
            <el-upload
              class="upload-demo"
              :action="config.apiRoot + '/upload'"
              :before-upload="beforeUpload"
              :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'img') }"
              :on-remove="(file, fileList) => { handleRemove(file, fileList, 'img') }"
              :file-list="vernue.img"
              list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-input class="input-wrap" type="textarea" v-model="vernue.descInfo"></el-input>
        </el-form-item>
        <h2></h2>
        <el-form-item label="">
          <el-button size="small" type="primary" @click="addOtherArea"></el-button>
        </el-form-item>
        <div class="other-area-wrap" v-for="(item, index) in vernue.other1" :key="index">
          <el-form-item label="">
            <el-input class="input-wrap" type="text" v-model="item.name"></el-input>
            <el-button size="small" type="primary" v-if="index !== 0" @click="deleOtherArea(index)"></el-button>
          </el-form-item>
          <el-form-item label="">
            <el-input class="input-wrap" type="text" v-model="item.area"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input class="input-wrap" type="text" v-model="item.height"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input class="input-wrap" type="text" v-model="item.count"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="item.feature">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox-group v-model="item.layout">
              <img class="bj-img" src="./../../static/img/bj-jy.png"/>
              <el-checkbox label=""></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-kz.png"/>
              <el-checkbox label=""></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-yh.png"/>
              <el-checkbox label=""></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-u.png"/>
              <el-checkbox label="U"></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-yg.png"/>
              <el-checkbox label=""></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-ds.png"/>
              <el-checkbox label=""></el-checkbox>
              <img class="bj-img" src="./../../static/img/bj-jh.png"/>
              <el-checkbox label=""></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="/">
            <el-select v-model="item.equipments" multiple placeholder="">
              <el-option-group
                v-for="group in equipmentList"
                :key="group.label"
                :label="group.label">
                <el-option
                  v-for="item in group.options"
                  :key="item.label"
                  :label="item.value"
                  :value="item.label">
                </el-option>
              </el-option-group>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-input class="input-wrap" type="textarea" placeholder="" v-model="item.otherEq"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div class="upload-wrap">
              <el-upload
                class="upload-demo"
                :action="config.apiRoot + '/upload'"
                :before-upload="beforeUpload"
                :on-success="(res, file, fileList) => { handleSuccess(res, file, fileList, 'otherImg', index) }"
                :on-remove="(file, fileList) => { handleRemove(file, fileList, 'otherImg', index) }"
                :file-list="item.img"
                list-type="picture">
                <el-button size="small" type="primary"></el-button>
              </el-upload>
            </div>
          </el-form-item>
        </div>
      </div>
      <br/>
      <el-form-item label="" prop="rateType">
        <el-select v-model="vernue.rateType" placeholder="">
          <el-option label="1" :value="1"></el-option>
          <el-option label="2" :value="2"></el-option>
          <el-option label="3" :value="3"></el-option>
          <el-option label="4" :value="4"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div>
          1
        </div>
        <div>
          2100%
        </div>
        <div>
          32424~125%12~610%6~220%250%100%
        </div>
        <div>
          40:00143360:0014~7336~16810%0:007~3168~7220%0:003~172~2450%0:0012480%0:00100%0:00
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" :loading="loading" @click="onSubmit(vernue)"></el-button>
      </el-form-item>
    </el-form>
    <!-- <el-button type="text" @click="open"> Message Box</el-button> -->
  </div>
</template>
<script>
import moment from 'moment'
import { areaProvince, areaCity, areaArea, equipmentList, projectList } from './../../api/common'
import { projectAdd } from './../../api/merchant'
import config from './../../config'
export default {
  data () {
    return {
      loading: false,
      config: config,
      vernue: {
        cg: 1,
        proId: null,
        days: [],
        areaId: '',
        extend2: {},
        amountImg: [],
        equipments: [],
        other: [{}], // 
        other1: [{
          layout: []
        }]
      },
      projectLists: [], // 
      equipmentList: [],
      provinceList: [],
      provinceId: '',
      cityId: '',
      cityList: [],
      areaList: [],
      fileList: [],
      options: [{
        label: '',
        value: 1
      }, {
        label: '',
        value: 2
      }, {
        label: '',
        value: 3
      }, {
        label: '',
        value: 4
      }, {
        label: '',
        value: 5
      }, {
        label: '',
        value: 6
      }, {
        label: '',
        value: 7
      }],
      rules: {
        proId: [{ required: true, message: '' }],
        proName: [{ required: true, message: '' }],
        areaId: [{ required: true, message: '' }],
        address: [{ required: true, message: '' }],
        // time: [{ required: true, message: '' }],
        startTime: [{ required: true, message: '' }],
        endTime: [{ required: true, message: '' }],
        days: [{ required: true, message: '' }],
        phone: [{ required: true, message: '' }],
        // otherPhone: [{ required: true, message: '' }],
        newTime: [{ required: true, message: '' }],
        stopCart: [{ required: true, message: '' }],
        stopAmount: [{ validator: this.validateAmount }],
        area: [{ required: true, message: '' }],
        height: [{ required: true, message: '' }],
        countNum: [{ required: true, message: '' }],
        type: [{ required: true, message: '' }],
        roomNum: [{ required: true, message: '' }],
        hotelStar: [{ required: true, message: '' }],
        checkInTime: [{ required: true, message: '' }],
        checkOutTime: [{ required: true, message: '' }],
        venueNum: [{ required: true, message: '' }],
        maxVenue: [{ required: true, message: '' }],
        maxVenueNum: [{ required: true, message: '' }],
        rateType: [{ required: true, message: '' }],
        thumbnail: [{ required: true, validator: this.validateThumbnail }]
      }
    }
  },
  beforeMount () {
    this.getProvince()
    this.getEquipmentList()
    this.getProjectList()
  },
  methods: {
    beforeUpload (file) {
      const isLt2kb = file.size / 1024 <= 200
      if (!isLt2kb) {
        this.$message.error(' 200kb!')
      }
      return isLt2kb
    },
    validateThumbnail (rule, value, callback) {
      let { thumbnail = [] } = this.vernue
      if (!value || !thumbnail.length) {
        return callback(new Error(''))
      } else {
        callback()
      }
    },
    validateAmount (rule, value, callback) {
      if (!value && (this.vernue.amountImg && !this.vernue.amountImg.length)) {
        return callback(new Error(''))
      } else {
        callback()
      }
    },
    handleRemove (file, fileList, type, key) {
      if (file && file.status === 'success') {
        let _fileList = fileList.map(item => {
          return {
            name: item.name,
            url: config.imgRoot + item.response.data.name
          }
        })
        if (type === 'amount') {
          this.vernue.amountImg = _fileList
        } else if (type === 'meal') {
          this.vernue.other[key].img = _fileList
        } else if (type === 'img') {
          this.vernue.img = _fileList
        } else if (type === 'otherImg') {
          this.vernue.other1[key].img = _fileList
        } else if (type === 'thumbnail') {
          this.vernue.thumbnail = _fileList
        }
      } else {
        return false
      }
    },
    handleSuccess (res, file, fileList, type, key) {
      if (type === 'amount') {
        if (!this.vernue.amountImg) {
          this.vernue.amountImg = []
        }
        this.vernue.amountImg.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'meal') {
        if (!this.vernue.other[key].img) {
          this.vernue.other[key].img = []
        }
        this.vernue.other[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'img') {
        if (!this.vernue.img) {
          this.vernue.img = []
        }
        this.vernue.img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'otherImg') {
        if (!this.vernue.other1[key].img) {
          this.vernue.other1[key].img = []
        }
        this.vernue.other1[key].img.push({
          name: res.data.name,
          url: config.imgRoot + res.data.name
        })
      } else if (type === 'thumbnail') {
        if (!this.vernue.thumbnail) {
          this.vernue.thumbnail = []
        }
        this.vernue.thumbnail = [{
          name: res.data.name,
          url: config.imgRoot + res.data.name
        }]
      }
    },
    //  
    addMeal () {
      this.vernue.other.push({})
    },
    deleMeal (index) {
      this.vernue.other = this.vernue.other.filter((item, key) => key !== index)
    },
    addOtherArea () {
      this.vernue.other1.push({
        layout: []
      })
    },
    deleOtherArea (index) {
      this.vernue.other1 = this.vernue.other1.filter((item, key) => key !== index)
    },
    getProjectList () {
      projectList().then((result) => {
        result.data && result.data.slice(0, 4).forEach(element => {
          this.projectLists = this.projectLists.concat(element.list)
        })
      })
    },
    getEquipmentList () {
      equipmentList().then((result) => {
        this.equipmentList = result.data && result.data.slice(0, 5).concat(result.data.slice(6, 8))
      })
    },
    // 
    getProvince () {
      areaProvince().then((result) => {
        this.provinceList = result.data
      })
    },
    getCity (id) {
      areaCity({ id }).then((result) => {
        this.cityList = result.data
      })
    },
    getArea (id) {
      areaArea({ id }).then((result) => {
        this.areaList = result.data.map(item => {
          return {
            id: item.areaId,
            name: item.name
          }
        })
      })
    },
    changeProvince (id) {
      this.cityId = ''
      this.vernue.areaId = ''
      this.areaList = []
      this.getCity(id)
    },
    changeCity (id) {
      this.vernue.areaId = ''
      this.getArea(id)
    },
    onSubmit () {
      this.$refs['vernue'].validate((valid) => {
        if (valid) {
          this.loading = true
          let _vernue = Object.assign({}, this.vernue)
          // _vernue.startTime = this.vernue.time && moment(this.vernue.time[0]).format('HH:mm')
          // _vernue.endTime = this.vernue.time && moment(this.vernue.time[1]).format('HH:mm')
          _vernue.days = this.vernue.days.join(':')
          _vernue.newTime = this.vernue.newTime ? moment(this.vernue.newTime).format('YYYY-MM') : ''
          _vernue.amountImg = (this.vernue.amountImg && this.vernue.amountImg.length) ? JSON.stringify(this.vernue.amountImg) : ''
          _vernue.other = this.vernue.other && JSON.stringify(this.vernue.other)
          _vernue.equipments = _vernue.equipments && _vernue.equipments.join(':')
          _vernue.extend2 = _vernue.extend2 && JSON.stringify(_vernue.extend2)
          _vernue.img = this.vernue.img && JSON.stringify(this.vernue.img)
          _vernue.other1 = this.vernue.other1 && JSON.stringify(this.vernue.other1)
          projectAdd(_vernue).then((result) => {
            this.$router.push('/project')
          }, () => {
            this.loading = false
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>



