<style lang="scss" scoped>
  .input-wrap {
    width: 230px;
  }
  .table-wrap {
    margin-top: 10px;
  }
  .pagination-wrap {
    overflow: hidden;
    >div {
      float: right;
    }
    margin-top: 10px;
  }
  .desc-wrap {
    padding: 10px;
  }
</style>
<template>
  <div>
    <div class="desc-wrap">
      <p>1</p>
      <p>2</p>
      <p>3</p>
      <p>4+</p>
    </div>
    <div>
      <el-input class="input-wrap" v-model="phone" placeholder=""></el-input>
      <el-button type="primary" @click="goSearch"></el-button>
    </div>
    <div class="table-wrap">
       <el-table
        :border="true"
        :data="tableData.list"
        style="width: 100%">
        <el-table-column
          prop="date"
          width="300"
          label="">
          <template slot-scope="scope">
            <p>{{ scope.row.filed.phone }}</p>
            <p>{{ scope.row.filed.name }}</p>
            <p>{{ scope.row.filed.email }}</p>
            <p>: {{ vipStatus[scope.row.vip] }}</p>
            <p>: {{ scope.row.filed.expTime }}</p>
          </template>
        </el-table-column>
        <!-- <el-table-column
          prop="name"
          label="">
          <template slot-scope="scope">
            <router-link to="examinePassDetail"><el-button type="text" size="small"></el-button></router-link>
          </template>
        </el-table-column> -->
        <el-table-column
          prop="name"
          label="">
          <template slot-scope="scope">
            <p>{{ scope.row.num1 }}</p>
            <p>{{ scope.row.num2 }}</p>
            <p>{{ scope.row.num3 }}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="succMoney"
          label="()">
          <template slot-scope="scope">
            <div>{{ (scope.row.succMoney || 0) / 100 }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="state"
          label="">
        </el-table-column>
        <el-table-column
          prop="sale"
          label="">
        </el-table-column>
        <el-table-column
          prop="succRate1"
          label="">
          <template slot-scope="scope">
            <p>{{ scope.row.orderSize }}</p>
            <p>{{ scope.row.succRate }}</p>
          </template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="">
          <template slot-scope="scope">
            <el-button @click="goMerchantFreeze(scope.row)" type="text" size="small">{{ scope.row.state === ''?'':'' }}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination-wrap">
        <div>
          <el-pagination
            background
            @current-change="this.goPage"
            layout="prev, pager, next"
            :total="tableData.total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { merchantInfo, merchantFreeze } from './../../api/platform'
export default {
  data () {
    return {
      vipStatus: {
        0: '',
        1: '',
        2: 'VIP'
      },
      phone: '',
      pageNum: 1,
      tableData: []
    }
  },
  beforeMount () {
    this.getMerchantInfo({pageNum: 1})
  },
  methods: {
    goMerchantFreeze (row) {
      merchantFreeze({ filedId: row.filed.id }).then((result) => {
        this.getMerchantInfo()
      })
    },
    getMerchantInfo (params) {
      merchantInfo({ ...params, phone: this.phone, pageNum: this.pageNum }).then((result) => {
        this.tableData = result.data
      })
    },
    goPage (pageNum) {
      this.pageNum = pageNum
      this.getMerchantInfo()
    },
    goSearch () {
      this.pageNum = 1
      this.getMerchantInfo()
    }
  }
}
</script>



